.footer {
  @include container;
  padding-top: 248px;
  padding-bottom: 100px;
  text-align: center;
}

.footer__wrapper {
  padding: 0 240px;
}

.footer h2 {
  @include reset;
  margin-bottom: 30px;
  color: $black-additional;
  font-family: 'Open Sans', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: -0.005em;
}

.footer p {
  @include reset;
  padding: 0 calc(50% - 240px);
  margin-bottom: 42px;
  color: $black;
  font-family: 'Open Sans', 'Arial', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 31px;
  opacity: 0.7;
}

.footer img {
  object-fit: cover;
  margin-bottom: 39px;
  height: 530px;

}

@media (max-width: $tablet-breakpoint) {
  .footer {
    padding-top: 155px;
    padding-bottom: 52px;
  }

  .footer__wrapper {
    padding: 0 35px;
  }

  .footer h2 {
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 20px;
  }

  .footer p {
    font-size: 17px;
    line-height: 31px;
    padding: 0;
    margin-bottom: 20px;
  }

  .footer img {
    margin-bottom: 20px;
    height: 545px;
  }
}

@media (max-width: $mobile-breakpoint) {
  .footer {
    padding-top: 119px;
    padding-bottom: 50px;
  }

  .footer__wrapper {
    padding: 0;
  }

  .footer h2 {
    font-size: 18px;
    line-height: 30px;
  }

  .footer p {
    padding: 0 18px;
  }

  .footer img {
    height: 250px;
  }
}
